<template>
  <div style="width: 100%; height: 80vh;">
     <iframe title="rentpress engrain sight map" ref="sightmap" id="sightmap" class="rentpress-engrain-sight-map" :src="engrainMapLink" width="100%" height="100%"></iframe>
  </div>
</template>

<script>
export default {
  name: "sight-map",
  data: function () {
    return {
      sightMap: {},
    };
  },
  watch: {
    units() {
      this.sightMap.setUnitNumberMatches(this.units, {filterEditing: true});
    }
  },
  props: {
    units: Array,
    link: String,
    options: Object,
    floorplans: Array,
    useModals: Boolean,
  },
  methods: {
    findFloorplanById(id) {
      var selectedFloorplan = "";
      if (typeof this.floorplans != undefined ? this.floorplans.length : "") {
        selectedFloorplan = this.floorplans.find(element => element.floorplan_code === id);
      }
      return selectedFloorplan ? selectedFloorplan : false;
    },
  },
  created() {
    let tag = document.createElement("script");
    tag.setAttribute("src", "https://sightmap.com/embed/api.js");
    document.head.appendChild(tag);
    tag.onload = () => { 
      this.sightMap = window.SightMap;
      this.sightMap.Embed(this.$refs.sightmap);
      let sightMap = this.sightMap
      let units = this.units;

      this.sightMap.on("ready", function () {
        sightMap.setUnitNumberMatches(units, {filterEditing: true});
        sightMap.disableUI(["unitList", "filters", "unitDetails"]);
      }, sightMap, units);
      this.sightMap.on("metrics.unitMap.unit.click", function (event) {
        const selectedFloorplan = this.findFloorplanById(JSON.parse(event.data.unit.floorPlan.name).provider_id);
        if (this.$parent.$options._componentTag === "floorplan-search") {
          if (this.useModals) {
            this.$parent.selectThisFloorplan(selectedFloorplan);
          } else {
            window.location = selectedFloorplan.floorplan_post_link;
          }
        }
      }, this);
    }
  },
  computed: {
    engrainMapLink() {
      if (this.link && this.link != undefined) {
        return this.link + "?enable_api=1&origin=" + location.protocol + "//" + location.host;
      }
      return "";
    },
  }
};
</script>
<style lang="css">
.rentpress-engrain-sight-map {
  border: 0;
}
</style>
